<template>
  <div class="home">
    <b-alert :show="alertCountdown" dismissible fade :variant="alertType" @dismiss-count-down="countDownChanged" >
      {{alertText}}
    </b-alert>
    <div class="requests-layout">
      <div class="public-rooms">
        <div class="div-hdr">
          <h5><b>طلبات العضويات</b></h5>
        </div>
        <div class="text-center text-danger my-2" v-show="isLoading">
          <b-spinner class="align-middle"></b-spinner>
        </div>
        <div class="requests-body" v-show="!isLoading">
          <b-row>
            <b-col lg="10" md="12" sm="12" v-if="requests.length == 0">
              <div class="pt-5 no-content" style="text-align:center">
                <img src="../assets/images/data-not-found.png">
                <h4> لا يوجد طلبات</h4>
              </div>
            </b-col>
            <b-col lg="10" md="12" sm="12" v-for="(request, index) in requests" :key="index" >
              <div class="request-col">
                <b-row align-v="center">
                  <b-col lg="7" md="7" sm="12">
                    <b-media vertical-align="center">
                      <template v-slot:aside>
                        <b-avatar class="image" :src="request.student.image"></b-avatar>
                      </template>
                      <h5 class="mt-3">{{request.student.full_name}} يريد ان ينضم الي غرفة {{request.room.name}}</h5>
                    </b-media>
                  </b-col>
                  <b-col lg="5" md="5" sm="12">
                    <div class="action-buttons">
                      <b-button variant="primary" @click="acceptRequestfunction(request.id)">قبول الطلب</b-button>
                      <b-button class="mr-2" variant="light" @click="refuseRequestfunction(request.id)"><i class="far fa-trash-alt delete-icon"></i></b-button>
                    </div>
                  </b-col>
                </b-row>
              </div>
            </b-col>
          </b-row>
        </div>
      </div>
    </div>

    <b-modal v-model="acceptRequest" centered title="قبول طلب الانضمام">
      <p class="my-4">هل تريد قبول انضمام الطالب للغرفة؟</p>
      <template v-slot:modal-footer>
        <b-button size="sm" variant="secondary" @click="$bvModal.hide('acceptRequest')">
          الغاء
        </b-button>
        <b-button size="sm" variant="primary" @click="actionRequest('accept')">
          قبول
        </b-button>
      </template>
    </b-modal>

    <b-modal v-model="refuseRequest" centered title="رفض طلب الانضمام">
      <p class="my-4">هل تريد رفض انضمام الطالب للغرفة؟</p>
      <b-textarea v-model="blockReason" placeholder="سبب الرفض"></b-textarea>
      <template v-slot:modal-footer>
        <b-button size="sm" variant="secondary" @click="$bvModal.hide('refuseRequest')">
          الغاء
        </b-button>
        <b-button size="sm" variant="primary" @click="actionRequest('blocked')">
          رفض
        </b-button>
      </template>
    </b-modal>
 </div>
</template>

<script>
  import store from "../store/index.js"
  import { sendRequest }  from "../utils/RequestHelper.js"
  
  export default {
    name: 'Home',
    data() {
      return {
        isLoading: false,
        alertCountdown: 0,
        alertType: '',
        alertText: '',
        acceptRequest: false,
        refuseRequest: false,
        seletedRequestID: null,
        blockReason: null,
        requests: []
      }
    },
    methods: {
      acceptRequestfunction(id) {
        this.blockReason = null;
        this.seletedRequestID = id;
        this.acceptRequest = true;
      },
      refuseRequestfunction(id) {
        this.blockReason = null;
        this.seletedRequestID = id;
        this.refuseRequest = true;
      },
      actionRequest(action) {
        this.isLoading = true;
        var message = null;
        if(action == 'accept')
          message = "تم قبول الطالب بنجاح";
        else
          message = "تم رفض الطالب بنجاح";
        var successCallback = () => {
          this.alertType = "success"
          this.alertText = message
          this.alertCountdown = process.env.VUE_APP_ALERT_COUNTDOWN;
          this.acceptRequest = false;
          this.refuseRequest = false;
          this.loadRequests();
        };
        var errorCallback = () => {
          this.isLoading = false;
          this.alertType = "danger"
          this.alertText = "خطأ .. حاول مرة اخرى لاحقا"
          this.alertCountdown = process.env.VUE_APP_ALERT_COUNTDOWN;
        };
        sendRequest('put', '/teacher/change-join-request-status/' + this.seletedRequestID + '/' + action + '?block_reason=' + this.blockReason , true, {} , successCallback, errorCallback);
      },
      countDownChanged(alertCountdown) {
        this.alertCountdown = alertCountdown
      },
      loadRequests() {
        this.isLoading = true;
        var successCallback = (response) => {
          response.data.data.forEach(element => {
            if(element.student){
              this.requests.push(element)
            }
          });
          this.isLoading = false;
        };
        var errorCallback = () => {
          this.isLoading = false;
          this.alertType = "danger"
          this.alertText = "خطأ .. حاول مرة اخرى لاحقا"
          this.alertCountdown = process.env.VUE_APP_ALERT_COUNTDOWN;
        };
        sendRequest('get', '/teacher/show-join-requests', true, null , successCallback, errorCallback);
      }
    },
    created() {
      if(store.getters.authToken == null) {
        store.commit("SET_TOKEN", null);
        store.commit("SET_USERNAME", null);
        this.$router.push({
          name: 'login'
        })
      }
      this.loadRequests();
    }
  }
</script>
